'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Alert, AlertDescription } from '@/components/ui/alert'

interface SelfieUploadProps {
  onAnalysisComplete?: (result: string) => void
}

const SelfieUpload = ({ onAnalysisComplete }: SelfieUploadProps) => {
  const [selectedFile, setSelectedFile] = useState<File | null>(null)
  const [previewUrl, setPreviewUrl] = useState<string | null>(null)
  const [isAnalyzing, setIsAnalyzing] = useState(false)
  const [analysisResult, setAnalysisResult] = useState<string | null>(null)
  const [error, setError] = useState<string | null>(null)

  const handleFileSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    setError(null)
    
    if (!file) return

    // Validate file type
    if (!file.type.startsWith('image/')) {
      setError('Please select a valid image file')
      return
    }

    // Validate file size (max 5MB)
    if (file.size > 5 * 1024 * 1024) {
      setError('File size must be less than 5MB')
      return
    }

    setSelectedFile(file)
    
    // Create preview URL
    const url = URL.createObjectURL(file)
    setPreviewUrl(url)
  }

  const handleUpload = async () => {
    if (!selectedFile) return

    setIsAnalyzing(true)
    setError(null)

    try {
      // Convert file to base64
      const base64 = await fileToBase64(selectedFile)
      
      const response = await fetch('/api/ai/analysis', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          image: base64,
          filename: selectedFile.name,
        }),
      })

      if (!response.ok) {
        throw new Error('Analysis failed. Please try again.')
      }

      const data = await response.json()
      setAnalysisResult(data.analysis)
      
      // Save to localStorage
      localStorage.setItem('lastStressAnalysis', data.analysis)
      
      if (onAnalysisComplete) {
        onAnalysisComplete(data.analysis)
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred during analysis')
    } finally {
      setIsAnalyzing(false)
    }
  }

  const fileToBase64 = (file: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader()
      reader.readAsDataURL(file)
      reader.onload = () => resolve(reader.result as string)
      reader.onerror = error => reject(error)
    })
  }

  const resetUpload = () => {
    setSelectedFile(null)
    setPreviewUrl(null)
    setAnalysisResult(null)
    setError(null)
    if (previewUrl) {
      URL.revokeObjectURL(previewUrl)
    }
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Daily Facial Analysis</CardTitle>
        <CardDescription>
          Upload a selfie to analyze your current stress levels using AI
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        {!selectedFile && (
          <div className="border-2 border-dashed border-border rounded-lg p-6 text-center">
            <input
              type="file"
              accept="image/*"
              onChange={handleFileSelect}
              className="hidden"
              id="selfie-upload"
            />
            <label
              htmlFor="selfie-upload"
              className="cursor-pointer space-y-2 block"
            >
              <div className="text-4xl">ðŸ“·</div>
              <p className="text-sm text-muted-foreground">
                Click to select a selfie
              </p>
              <p className="text-xs text-muted-foreground">
                Supports JPG, PNG (max 5MB)
              </p>
            </label>
          </div>
        )}

        {previewUrl && (
          <div className="space-y-4">
            <div className="relative">
              <img
                src={previewUrl}
                alt="Selected selfie"
                className="w-full max-w-sm mx-auto rounded-lg object-cover aspect-square"
              />
            </div>
            
            <div className="flex gap-2 justify-center">
              <Button
                onClick={handleUpload}
                disabled={isAnalyzing}
                className="flex-1 max-w-xs"
              >
                {isAnalyzing ? 'Analyzing...' : 'Analyze Stress Level'}
              </Button>
              <Button
                variant="outline"
                onClick={resetUpload}
                disabled={isAnalyzing}
              >
                Reset
              </Button>
            </div>
          </div>
        )}

        {error && (
          <Alert variant="destructive">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        {analysisResult && (
          <Alert>
            <AlertDescription>
              <strong>Analysis Result:</strong> {analysisResult}
            </AlertDescription>
          </Alert>
        )}
      </CardContent>
    </Card>
  )
}

export default SelfieUpload
